<template>
    <div class="main">
        <span class="text" v-if="showText">{{textForInsert}}</span>
        <div class="navigation">
            <button class="next" v-if="backShow" v-on:click="prev_next_event('prev')">Назад</button>
            <button class="next" v-on:click="prev_next_event('next')">{{btnNext}}</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Text-block",
        data() {
            return {
                showText: true,
                Text1: 'Миледи, стоит мне признать, что дня сиго мелодия мне кажется уж несколько торжественной и праздной! Быть может я ее и не заметил бы, да и не стал бы отвлекаться от великих дел, свершаемых под вкус теплого чая. Однако же мелодии сей нотки, сумели пронзить меня любопытством и заставили обернуться, поймав Ваш дивный добродушный взгляд. А потому, Наталья, смею поздравить Вас с Днем Рождения и дабы наполнить остаток дня яркими красками, позвольте провести Вас под руку по тропе сиго письма дальше :)',
                Text2: 'И вот, продолжая чтение сиго письма, шаг за шагом ты идешь дальше по тропе, в то время как тропа развивается на сотни других тропинок, а те в свою очередь на тысячи других. А тем временем, секунда за секундой время бежит, часовая стрелка крутится, а приведенные в движение шестеренки столь искусно и целесообразно взаимодействуют между собой, что вызывают те или иные события, которые в конечном счете заставят повернуть тебя на ту самую тропу, что и была уготовлена судьбой. Сей сложную и запутанную игру, мы именуем Жизнью. И коль мне посчастливилось сейчас оказаться прямо здесь, невзирая на тот факт [ Довольно незначимый :) ], что мы с тобою незнакомы, думаю ты не будешь против, если при помощи ключа я отворю занавес Вселенной, за которым прячется сей сложный механизм, дабы вмешаться в ход истории.\n',
                Text3: 'Что ж. Всего лишь несколько вращений часовой стрелки и вуаля, 1830 год. Добро пожаловать в новое государство на карте Европы - Бельгию! Поблагодарим за сие событие месье Талейрана, но сейчас не об этом. Вероятно ты задашь вопрос, что мы здесь забыли? Терпение мадемуазель :) Для начала нам надобно преодолеть все эти узкие улочкие Антверпена, чтобы прийти по указанному адресу к этому дому, дабы представить тебе господина художника Фердинанда де Бракелера. Вероятно ты будешь удивлена увидев на его картинах себя? Даже сложно представить сколько труда ему стоило, чтобы выразить на картинах с помощью красок эти черты лица, вложив в них уверенный  взгляд и наполненную радостью и жизнелюбием улыбку. Пройдет 168 лет с того момента как высохли краски и тебе будет суждено родиться. И хоть картин нам уже не найти, с помощью незримых законов вселенной сквозь века истории передадутся тебе эти черты, дабы ты как можно чаще улыбалась и все время с уверенностью смотрела в завтрашний день.',
                Text4: 'А тем времнем, под звуки льющейся мелодии, из Бельгии века 19-ого проложим мост в век эдак 15-ый, дабы очутиться во владениях английской короны. Что может быть прекрасней, чем прогулка по королевским садам? Прохладный ветерок... Кристально чистая вода в пруду... Таинственные взгяды статуй с ликами божеств древних элинов... Самое время сорвать с ближайшей яблони сочный плод и вспомнить миф о Парисе, трех богинях и яблоке, что он должен был вручить прекраснейшей из них. Полагаю этот миф тебе известен. А потому предлагаю сразу же его забыть, развеяв словно пыль все эти образы мифических богов, дабы вложить сей яблоко в твою ладонь, пожелав сохранить на многие десятилетия столь знатную красоту и кристально чистое сердце, в десятки раз раумножающие ее. А пока что... В этот волнительный момент под взоры королевской стражи... Перед тем как нас повесят за кражу королевских яблок, предлагаю незамедлительно делать ноги!!!',
                Text5: 'Увлекая тебя за руку все далее и далее вглубь веков, должен признать, время пролетело молниеносно и незаметно. Но сколько бы ни было увлекательно путешествие, всегда нужно возвращаться. И прежде чем мне будет суждено завершить его окончательно и раствориться в веках истории, позволь вложить в твое сердце искру, что охватит его пламенем веры и надежды в светлое будущее. И пусть это пламя всегда согревает тебя и подсказывает на какую из тропинок свернуть! :)\n',
                currentText: ''
            }
        },
        methods: {
            prev_next_event(direction) {
                console.log('length text1', this.Text1.length);
                console.log('length text2', this.Text2.length);
                console.log('length text3', this.Text3.length);
                console.log('length text4', this.Text4.length);
                console.log('length text5', this.Text5.length);

                if (direction === 'prev') {
                    if (this.$store.getters.getTextPage === 2) {
                        this.$store.dispatch('ACT_ANTI_CLOCKWISE_ROTATION');
                        this.currentText = this.Text1;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(500);
                        this.$store.dispatch('ACT_DEC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 3) {
                        this.$store.dispatch('ACT_ANTI_CLOCKWISE_ROTATION');
                        this.currentText = this.Text2;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(810);
                        this.$store.dispatch('ACT_DEC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 4) {
                        this.$store.dispatch('ACT_ANTI_CLOCKWISE_ROTATION');
                        this.currentText = this.Text3;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(990);
                        this.$store.dispatch('ACT_DEC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 5) {
                        this.$store.dispatch('ACT_ANTI_CLOCKWISE_ROTATION');
                        this.currentText = this.Text4;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(930);
                        this.$store.dispatch('ACT_DEC_PAGE');
                    }
                }

                else {
                    if (this.$store.getters.getTextPage === 0) {
                        this.currentText = this.Text1;
                        this.changeCounter(500);
                        this.$store.dispatch('ACT_INC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 1) {
                        this.$store.dispatch('ACT_CLOCKWISE_ROTATION');
                        this.currentText = this.Text2;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(810);
                        this.$store.dispatch('ACT_INC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 2) {
                        this.$store.dispatch('ACT_CLOCKWISE_ROTATION');
                        this.currentText = this.Text3;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(990);
                        this.$store.dispatch('ACT_INC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 3) {
                        this.$store.dispatch('ACT_CLOCKWISE_ROTATION');
                        this.currentText = this.Text4;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(930);
                        this.$store.dispatch('ACT_INC_PAGE');
                    }
                    else if (this.$store.getters.getTextPage === 4) {
                        this.$store.dispatch('ACT_CLOCKWISE_ROTATION');
                        this.currentText = this.Text5;
                        this.$store.dispatch('ACT_COUNT_SYMBOLS_IS_NULL');
                        this.changeCounter(465);
                        this.$store.dispatch('ACT_INC_PAGE');
                    }
                }
            },
            changeCounter: function(count) {
                const self = this;
                let temp = 0;
                let start = Date.now();
                let timer = setInterval(function() {
                    temp += 1;
                    let timePassed = Date.now() - start;
                    self.$store.dispatch('ACT_INC_COUNT_SYMBOLS');
                    if (timePassed >= 50*count && temp === count) {
                        clearInterval(timer);
                    }
                }, 50);
            }
        },
        computed: {
            textForInsert () {
                return this.currentText.slice(0, this.$store.getters.getTextCountSymbols);
            },
            btnNext () {
                if (this.$store.getters.getTextPage === 0) {
                    return 'Начать просмотр';
                } else {
                    return 'Далее'
                }
            },
            backShow() {
                return this.$store.getters.getTextPage > 0
            }
        }
    }
</script>

<style scoped lang="scss">
.main {
    max-width: 40em;
    background-color: rgba(0, 0, 0, 0.75);
    color: white;
    border-radius: .5em;
    font-style: italic;
    font-size: 20px;
    padding: 1em;
    font-family: "Palatino Linotype", serif;
    display: flex;
    text-align: left;
    margin: 0 auto;
    flex-direction: column;
    .text {
        padding-bottom: 1em;
        width: 35em;
    }
    .navigation {
        margin-left: auto;
    }
    .next {
        font: inherit;
        padding: 0;
        border: none;
        color: wheat;
        background-color: transparent;
        cursor: pointer;
        margin-left: auto;
        margin-right: 2em;
        &:hover {
            color: #ffde75;
        }
        &:active {
            outline: none;
            padding: 0;
            border: none;
            font: inherit;
            color: #ffbf4b;
            background-color: transparent;
            cursor: pointer;
            margin-left: auto;
            margin-right: 2em;
        }
        &:focus {
            outline: none;
            border: none;
            font: inherit;
        }
    }
}
</style>