<template>
    <div class="main" v-if="img">
        <img src="../assets/Nataly-img1.png" alt="" v-if="img1"  class="img" :style="{opacity: imgOpacity1}">
        <img src="../assets/Nataly-img2.png" alt="" v-if="img1"  :style="{opacity: imgOpacity2}">
        <img src="../assets/Nataly-img3.png" alt="" v-if="img2" class="img" :style="{opacity: imgOpacity1}">
        <img src="../assets/Nataly-img4.png" alt="" v-if="img2" :style="{opacity: imgOpacity2}">
    </div>
</template>

<script>
    export default {
        name: "Image-block",
        methods: {

        },
        computed: {
            imgOpacity1() {
                if (this.$store.getters.getTextPage === 3) {
                    return (this.$store.getters.getTextCountSymbols - 500) * 0.01;
                } else if (this.$store.getters.getTextPage === 4) {
                    return (this.$store.getters.getTextCountSymbols - 402) * 0.01;
                }
                else {
                    return 0;
                }
            },
            imgOpacity2() {
                if (this.$store.getters.getTextPage === 3) {
                    return (this.$store.getters.getTextCountSymbols - 525) * 0.01;
                } else if (this.$store.getters.getTextPage === 4) {
                    return (this.$store.getters.getTextCountSymbols - 570) * 0.01;
                }
                else {
                    return 0;
                }
            },
            img1() {
                return this.$store.getters.getTextPage < 4
            },
            img2() {
                return this.$store.getters.getTextPage === 4
            },
            img() {
                return this.$store.getters.getTextPage < 5
            }
        }
    }

</script>

<style scoped lang="scss">
    .main {
        max-width: 100%;
        background-color: rgba(0, 0, 0, 0.75);
        border-radius: .5em;
        padding: 1em;
        display: flex;
        flex-direction: column;
        /*min-width: 10em;*/
        .img {
            margin-bottom: 1em;
        }
    }
</style>