<template>
    <div class="circle">
        <div class="main">
            <div class="date-pointer">
                <archway-icon class="icon-1"/>
                <archway-icon class="icon-2"/>
                <archway-icon class="icon-3"/>
                <archway-icon class="icon-4"/>
                <archway-icon class="icon-5"/>
                <img class="clock-pointer" src="../assets/clock-pointer.png" alt="cp" :style="{ transform: `rotate(${pointerRotate}deg)`}">
                <div class="date" id="date">
                    <h1>{{year}}</h1>
                    <div class="separator">
                        <div>
                            <hr>
                            <hr>
                            <hr>
                        </div>
                        <div class="circle-inside"></div>
                        <img src="../assets/dec-element.png" alt="">
                        <div>
                            <hr>
                            <hr>
                            <hr>
                        </div>
                    </div>
                    <h3>19 февраля</h3>
                </div>
                <img class="clock-pointer-2" src="../assets/clock-pointer.png" alt="cp">
                <archway-icon class="icon-none"/>
            </div>
        </div>
    </div>
</template>

<script>
    import ArchwayIcon from "vue-material-design-icons/Arch";

    export default {
        components: {
            ArchwayIcon
        },
        data(){
            return {

            }
        },
        name: "Clock",
        computed: {
            pointerRotate() {
                return this.$store.getters.getRotateValue;
            },
            year() {
                if (this.$store.getters.getRotateValue <= 45) {
                    return 2019;
                } else if (this.$store.getters.getRotateValue <= 90 && this.$store.getters.getRotateValue >= 45) {
                    return 2019  + Math.round(45*4.2 - this.$store.getters.getRotateValue*4.2);
                } else if (this.$store.getters.getRotateValue <= 135 && this.$store.getters.getRotateValue > 90) {
                    return 2019  + Math.round(45*6.5 - this.$store.getters.getRotateValue*6.5);
                } else if (this.$store.getters.getRotateValue <= 180 && this.$store.getters.getRotateValue > 135) {
                    return 2019  + Math.round(45*6.5 - this.$store.getters.getRotateValue*6.5 + (this.$store.getters.getRotateValue-135)*19.5);
                }
            }
        }
    }
</script>

<style scoped lang="scss">

    #date {
        transition: font-size 3s, color 2s;
    }

    .circle {
        /*background-color: rgba(0, 0, 0, 0.5);*/
        /*border: 2px solid #d0d0d0;*/
        /*border-radius: 0 100% 100% 0 / 0 60% 60% 0;*/

        .main {
            margin: 1em 1em 1em 0;
            background-color: rgba(0, 0, 0, 0.75);
            border-radius: .5em;
            width: 21em;
            display: flex;
            border: 2px solid #d0d0d0;
            border-radius: 0 100% 100% 0 / 0 60% 60% 0;
            .date-pointer {
                max-width: 40em;
                color: white;
                font-style: italic;
                font-size: 20px;
                padding: 1em;
                font-family: "Palatino Linotype", serif;
                display: flex;
                text-align: left;
                flex-direction: column;
                align-items: center;
                /*@keyframes moving {*/
                    /*100% {transform: rotate(+45deg);}*/

                /*}*/
                .icon-1 {
                    transform-origin: 21px 239px;
                    transform: rotate(0deg);
                    margin-bottom: 10px;
                }
                .icon-2 {
                    position: absolute;
                    transform-origin: 21px 239px;
                    transform: rotate(45deg);
                    margin-bottom: 10px;
                }
                .icon-3 {
                    position: absolute;
                    transform-origin: 21px 239px;
                    transform: rotate(90deg);
                    margin-bottom: 10px;
                }
                .icon-4 {
                    position: absolute;
                    transform-origin: 21px 239px;
                    transform: rotate(135deg);
                    margin-bottom: 10px;
                }
                .icon-5 {
                    position: absolute;
                    transform-origin: 21px 239px;
                    transform: rotate(180deg);
                    margin-bottom: 10px;
                }
                .icon-none {
                    transform-origin: 17px 185px;
                    transform: rotate(0deg);
                    margin-bottom: 35px;
                    opacity: 0;
                }
                .circle-inside {
                    position: absolute;
                    width: 13.7em;
                    height: 18.5em;
                    border: 1px solid #d0d0d0;
                    border-radius: 0 100% 100% 0% / 0 80% 80% 0;
                    margin-left: 54px;
                    @-moz-document url-prefix() {
                        margin-left: 110px !important;
                    }
                }
                .clock-pointer {
                    transform-origin: 17px 185px;
                    /*transform: rotate(0deg);*/
                    padding-bottom: 10px;
                    animation: moving 2s 1 ease;
                }

                .clock-pointer-2 {
                    transform: rotate(0);
                    padding-bottom: 50px;
                    opacity: 0;
                }

                h1 {
                    font-size: 3em;
                }
                h1, h3 {
                    margin: 0;
                    padding: 0;
                }

                .separator {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    img {
                        padding: 0.2em 0.2em;
                    }

                    hr {
                        width: 2em
                    }
                }
            }
        }
    }

    .rotate-enter-active {
        transition: all, .5ms;
    }

    .rotate-enter-to {
        transform: rotate(+45deg);
    }

</style>

<style lang="scss">
    .circle {
        .icon-1, .icon-2, .icon-3, .icon-4, .icon-5, .material-design-icon > .material-design-icon__svg {
            height: 2em;
            width: 2em;
            color: #d6d6d6;
        }
    }
    .newClass {
        color: white;
    }
</style>