import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        clock: {
            rotateValue: 0
        },
        text: {
            page: 0,
            CountSymbols: 0,
        },
        image: {

        }
    },
    mutations: {
        // Clock
        CLOCKWISE_ROTATION(state) {
            let temp = 0;
            let start = Date.now();
            let timer = setInterval(function () {
                let timePassed = Date.now() - start;
                state.clock.rotateValue += 1;
                temp += 1;
                if (timePassed >= 25 * 45 && temp === 45) {

                    clearInterval(timer);
                }
            }, 25);
        },
        ANTI_CLOCKWISE_ROTATION(state) {
            let temp = 0;
            let start = Date.now();
            let timer = setInterval(function () {
                let timePassed = Date.now() - start;
                state.clock.rotateValue -= 1;
                temp += 1;
                if (timePassed >= 25 * 45 && temp === 45) {

                    clearInterval(timer);
                }
            }, 25);
        },

        // Text
        INC_PAGE(state) {
            state.text.page += 1;
        },
        DEC_PAGE(state) {
            state.text.page -= 1;
        },
        INC_COUNT_SYMBOLS(state) {
            state.text.CountSymbols += 1;
        },
        COUNT_SYMBOLS_IS_NULL(state) {
            state.text.CountSymbols = 0;
        }
    },

    actions: {
        // Clock
        ACT_CLOCKWISE_ROTATION ({commit}) {
            commit('CLOCKWISE_ROTATION')
        },
        ACT_ANTI_CLOCKWISE_ROTATION ({commit}) {
            commit('ANTI_CLOCKWISE_ROTATION')
        },
        ACT_INC_PAGE({commit}) {
            commit('INC_PAGE')
        },
        ACT_DEC_PAGE({commit}) {
            commit('DEC_PAGE')
        },
        ACT_INC_COUNT_SYMBOLS({commit}) {
            commit('INC_COUNT_SYMBOLS')
        },
        ACT_COUNT_SYMBOLS_IS_NULL({commit}) {
            commit('COUNT_SYMBOLS_IS_NULL')
        }

        // Text
    },

    getters: {
        getRotateValue: (state) => state.clock.rotateValue,
        getTextPage: (state) => state.text.page,
        getTextCountSymbols: (state) => state.text.CountSymbols
    }
})
